.about-container {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Creates two equal-width columns */
  gap: 20px; /* Adds some space between the columns */
  align-items: start; /* Aligns the content to the top */
  justify-content: center; /* Horizontally centers the content */
  padding: 20px; /* Adds some space around the content */
  margin: 0 auto; /* Adds equal whitespace on the left and right */
  max-width: 1200px; /* Adjust as needed */
  padding-top: 40px;
  color: #000000; /* Changes the font color */
  font-family: "Georgia", "Segoe UI", "Helvetica Neue", sans-serif;
}

.description {
  padding: 20px; /* Adds some space inside the description */
  position: relative; /* Needed for the pseudo-element */
  text-align: left;
}

.description::after {
  content: "";
  position: absolute;
  right: -10px; /* Adjust as needed */
  top: 0;
  bottom: 0;
  width: 1px; /* Adjust as needed */
  background: linear-gradient(
    to bottom,
    #70a58f,
    #70a58f
  ); /* Adjust as needed */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Adds a drop shadow */
  border-radius: 50%; /* Makes the line slightly rounded */
  margin: 20px 0; /* Adds some space above and below the line */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  /* Adds a drop shadow */
  border-radius: 70%;
  /* Makes the line slightly rounded */
}

.lets-talk {
  font-family: "Sofia Pro Italic";
  text-decoration: none;
  color: black;
  position: relative; /* Required for positioning the pseudo-element */
  display: inline-block; /* Ensures the pseudo-element aligns properly under the text */
}

/* Add this after your .lets-talk style */
.lets-talk::after {
  content: "";
  display: block; /* Creates a new line for the pseudo-element */
  width: 100%; /* Matches the width of the 'Let's Talk!' text */
  height: 2px; /* The thickness of the dashed line */
  background: repeating-linear-gradient(
    to right,
    #978431,
    #978431 5px,
    /* Decrease the width of each dash */ transparent 5px,
    /* Decrease the spacing between dashes */ transparent 10px
      /* Continue the pattern */
  );
  position: absolute;
  bottom: -5px; /* Adjust the space below the text */
  left: 0;
}

@media (max-width: 768px) {
  .description::after {
    position: relative;
    right: auto;
    top: auto;
    bottom: auto;
    height: 1px;
    width: 100%;
    left: 0;
    margin: 20px 0; /* Adds some space above and below the line */
  }
}

.store-logo {
  width: 100%;
  max-width: 150px; /* Adjust as needed */
  margin-bottom: 20px; /* Adds some space below the logo */
  border-radius: 50%; /* Makes the logo slightly rounded */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Adds a drop shadow */
  /* Center the logo image in the center of the grid */
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.description,
.sidebar {
  padding: 20px; /* Adds some space inside the description and sidebar */
}

.sidebar {
  padding: 20px; /* Adds some space inside the sidebar */
  text-align: left; /* Aligns the text to the right */
  position: sticky; /* Makes the sidebar stick to the top of the viewport */
  top: 0; /* Aligns the sidebar with the top of the viewport */
  color: #000000;
  /* Sofia Pro Bold font family */
  font-family: "Sofia Pro Bold", sans-serif;
  /* Changes the font family */
}

.sidebar p {
  font-family: Georgia, "Times New Roman", Times, serif;
  color: #000000;
}

@media (max-width: 768px) {
  .about-container {
    grid-template-columns: 1fr; /* Stacks the columns vertically on smaller screens */
  }

  .sidebar {
    position: static; /* Resets the position on smaller screens */
    text-align: left; /* Aligns the text to the left on smaller screens */
  }
}

.about-container h1,
.about-container h2,
.about-container h3,
.about-container h4,
.about-container h5,
.about-container h6 {
  color: #ffa5b2; /* Soft Pink */
}

/* Responsive Layout */

@media (max-width: 768px) {
  .about-container {
    grid-template-columns: 1fr; /* Stacks the columns vertically on smaller screens */
  }

  .sidebar {
    text-align: left; /* Aligns the text to the left on smaller screens */
  }
}
