.specials-container {
  text-align: center;
  font-family: "Roboto", "Segoe UI", "Helvetica Neue", sans-serif;
}

.specials-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  justify-items: center;

  @media (max-width: 1024px) {
    grid-template-columns: repeat(
      2,
      1fr
    ); /* Changes to 2 columns on larger screens */
  }

  @media (max-width: 768px) {
    grid-template-columns: 1fr; /* Changes to 1 column on medium screens */
  }
}

.special-card {
  border: 1px solid #ccc;
  padding: 20px;
  max-width: 300px;
  transition: transform 0.3s ease-in-out;
  border-radius: 12px; /* Add rounded corners to the cards */
