.special-card img {
  width: 100%;
  height: 200px; /* Set a specific height for the images */
  object-fit: cover; /* Cover the whole area of the container */
  border-radius: 12px; /* Add rounded corners to the images */
}

.special-card:hover {
  transform: scale(1.05); /* Scales the card up slightly */
}

.special-card h2 {
  color: #66cc7f; /* Changes the color of the flavor name to Mint Green */
}
