body {
  overflow-x: hidden;
}

.contact-container {
  text-align: center;
  color: #978431;
  font-family: "Roboto", "Segoe UI", "Helvetica Neue", sans-serif;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-items: center;
  margin: auto; /* Centers the grid */
  width: 90%; /* Adjust this value as needed */
}

@media (max-width: 412px) {
  .contact-grid {
    grid-template-columns: 1fr; /* Makes the grid have 1 column on small screens */
  }
}

.map iframe {
  width: 100%; /* Makes the iframe take up the full width of its container */
  height: 100%;
}

.form {
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.15);
}

.form input,
.form textarea {
  width: 100%;
  padding: 15px;
  margin-bottom: 15px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.15);
  font-family: "Roboto", "Segoe UI", "Helvetica Neue", sans-serif;
  font-size: 16px;
}

.form button {
  width: 100%;
  padding: 15px;
  border: none;
  border-radius: 20px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.15);
  font-family: "Roboto", "Segoe UI", "Helvetica Neue", sans-serif;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

/* Animation in CSS for progress bar on Send Button */
@keyframes loading {
  0% {
    background-position: 0% 0;
  }
  100% {
    background-position: 100% 0;
  }
}

.Button {
  /* ... (rest of your button styles) */

  &.loading {
    position: relative;
    overflow: hidden;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.5),
        transparent
      );
      transform: translateX(-100%);
      animation: loading 2s infinite;
    }
  }
}
